import React from "react";
import Grid from "@mui/material/Grid";
import Divider from "@mui/material/Divider"
import List from "@mui/material/List"
import ListItemText from '@mui/material/ListItemText';
import {useTranslation} from "react-i18next"
import {formatDescriptionToList} from "../utils";
import {useCheckPrint} from "../hooks/usePrint";

export default function Ability() {
	const [checkPrint] = useCheckPrint();
	const subTitleStyle = {
		fontWeight: 700,
		fontSize: "18px"
	}
	const {t} = useTranslation();
	let frontList = formatDescriptionToList(t("ability.frontList"))
	let backEndList = formatDescriptionToList(t("ability.backEndList"))
	let web3List = formatDescriptionToList(t("ability.web3List"))
	let aboutList = formatDescriptionToList(t("ability.aboutList"))
	return (
		<React.Fragment>
			<Divider/>
			<h2>{t("ability.title")}</h2>
			<div>
				<Grid container>
					<Grid item xs={checkPrint ? 4 : 12} sm={6} md={4} lg={4}>
						<p style={subTitleStyle}>{t("ability.frontEnd")}</p>
						<List>
							{frontList.map((item, index) => {
								return (<ListItemText primary={item} key={item + index}/>)
							})}
						</List>
					</Grid>
					<Grid item xs={checkPrint ? 4 : 12} sm={6} md={4} lg={4}>
						<p style={subTitleStyle}>{t("ability.backEnd")}</p>
						<List>
							{backEndList.map((item, index) => {
								return (<ListItemText primary={item} key={item + index}/>)
							})}
						</List>
					</Grid>
					<Grid item xs={checkPrint ? 4 : 12} sm={6} md={4} lg={4}>
						<p style={subTitleStyle}>{t("ability.web3")}</p>
						<List>
							{web3List.map((item, index) => {
								return (<ListItemText primary={item} key={item + index}/>)
							})}
						</List>
					</Grid>
				</Grid>
			</div>
			<List>
				{aboutList.map((item, index) => {
					return (<ListItemText key={item + index}>{item}</ListItemText>)
				})}
			</List>
			<Divider/>
		</React.Fragment>
	)
}
