import React from "react"
import Button from "@mui/material/Button"
import {useTranslation} from 'react-i18next'

export default function Title() {
	const titleStyle = {
		textAlign: "center"
	}
	const toggleAreaStyle = {
		display: "flex",
		justifyContent:"space-around",
		paddingBottom:"10px",

	}
	const {t, i18n} = useTranslation()

	function toggleLanguage(langText = "") {
		if (langText) {
			i18n.changeLanguage(langText)
		}
	}

	/**
	 * @return {string}
	 */
	function BtnBind(langText = "") {
		let result = "outlined"
		if (langText === i18n.language) {
			result = "contained"
		}
		return result
	}

	return (
		<React.Fragment>
			<h1 style={titleStyle}>{t('title')}</h1>
			{/*<div style={toggleAreaStyle}>*/}
			{/*	<Button variant={BtnBind("en")} onClick={toggleLanguage.bind(this, "en")}>English</Button>*/}
			{/*	<Button variant={BtnBind("zh-TW")} onClick={toggleLanguage.bind(this, "zh-TW")}></Button>*/}
			{/*	<Button variant={BtnBind("zh-CN")} onClick={toggleLanguage.bind(this, "zh-CN")}></Button>*/}
			{/*</div>*/}
		</React.Fragment>
	)
}
