import React from "react";
import {useTranslation} from "react-i18next"
import {formatDescriptionToList} from "../utils";
import styles from "./project.module.css"

// 
function ProjectItem({pro}) {
	const {
		labelTitle,
		labelDuty,
		labelSummary,
		labelContent,
		labelEffect,
		valueTitle,
		valueDuty,
		valueSummary,
		valueContent,
		valueEffect,
	} = pro
	let contentList = formatDescriptionToList(valueContent)
	return (
		<div className={styles.projectItem}>
			<p><strong className={styles.label}>{valueTitle}</strong></p>
			<p><strong className={styles.label}>{valueDuty}</strong></p>
			<p><strong className={styles.label}>{labelSummary}</strong>{valueSummary}</p>
			<div><strong className={styles.label}>{labelContent}</strong>{contentList.map((item, index) => (
				<p className={styles.contentItem} key={index} dangerouslySetInnerHTML={{__html: item}}/>))}</div>
			<p><strong className={styles.label}>{labelEffect}</strong>{valueEffect}</p>
		</div>
	)
}


export default function Project() {
	const {t} = useTranslation();

	function formatPro(proTypeName = "") {

		let pro = {}
		if (proTypeName) {
			pro = {
				labelTitle: t(`project.${proTypeName}.labelTitle`),
				labelDuty: t(`project.${proTypeName}.labelDuty`),
				labelSummary: t(`project.${proTypeName}.labelSummary`),
				labelContent: t(`project.${proTypeName}.labelContent`),
				labelEffect: t(`project.${proTypeName}.labelEffect`),

				valueTitle: t(`project.${proTypeName}.valueTitle`),
				valueDuty: t(`project.${proTypeName}.valueDuty`),
				valueSummary: t(`project.${proTypeName}.valueSummary`),
				valueContent: t(`project.${proTypeName}.valueContent`),
				valueEffect: t(`project.${proTypeName}.valueEffect`),

			}
		}
		return pro
	}


	return (
		<React.Fragment>
			<h2>{t("project.title")}</h2>
			<ProjectItem pro={formatPro("pro0")}/>
			<ProjectItem pro={formatPro("pro1")}/>
			<ProjectItem pro={formatPro("pro2")}/>
			<ProjectItem pro={formatPro("pro3")}/>
			<ProjectItem pro={formatPro("pro4")}/>
		</React.Fragment>
	)
}
