import {useState} from "react";

const useCheckPrint = function (defaultCheck = false) {
	const [checkPrint, setCheckPrint] = useState(defaultCheck)
	// 
	window.addEventListener("beforeprint", function () {
		setCheckPrint(true);
	})
	window.addEventListener("afterprint", function () {
		setCheckPrint(false);
	})
	return [checkPrint, setCheckPrint]
}

export {
	useCheckPrint
}
