import React from "react";

import Grid from "@mui/material/Grid";
import {useTranslation} from "react-i18next"
import styles from "./personInfo.module.css"
import {useCheckPrint} from "../hooks/usePrint";

export default function PersonInfo() {
	const [checkPrint] = useCheckPrint();

	const {t} = useTranslation();
	return (
		<React.Fragment>
			<h2>{t("personInfo.title")}</h2>
			<div className={styles.textStyle}>
				<Grid container>
					<Grid item xs={checkPrint ? 6 : 12} sm={4} md={4} lg={4}>
						{t("personInfo.base")}
					</Grid>
					<Grid item xs={checkPrint ? 6 : 12} sm={4} md={4} lg={4}>
						{t("personInfo.exp")}
					</Grid>
					<Grid item xs={checkPrint ? 6 : 12} sm={4} md={4} lg={4}>
						{t("personInfo.contact")}
					</Grid>
					<Grid item xs={checkPrint ? 6 : 12} sm={4} md={4} lg={4}>
						{t("personInfo.email")}
					</Grid>
					<Grid item xs={checkPrint ? 6 : 12} sm={4} md={4} lg={4}>
						{t("personInfo.university")}
					</Grid>
					<Grid item xs={checkPrint ? 6 : 12} sm={4} md={4} lg={4}>
						{t("personInfo.blog")}
					</Grid>
					<Grid item xs={checkPrint ? 6 : 12} sm={4} md={4} lg={4}>
						{t("personInfo.intention")}
					</Grid>
				</Grid>
			</div>
		</React.Fragment>
	)
}
