import React from "react";
import Grid from "@mui/material/Grid";
import List from "@mui/material/List";
import ListItem from "@mui/material/ListItem"
import AccessTimeIcon from "@mui/icons-material/AccessTime"

import styles from "./experience.module.css"
import {useTranslation} from "react-i18next"
import {formatDescriptionToList} from "../utils";
import {useCheckPrint} from "../hooks/usePrint";

// 
function ExperienceItem({exp}) {
	const [checkPrint] = useCheckPrint();
	const {t} = useTranslation();
	const {
		workTime,
		position,
		duty,
		dutyDescription,
		summaryDescription
	} = exp


	let dutyList = formatDescriptionToList(dutyDescription)
	let summaryList = formatDescriptionToList(summaryDescription)
	return (
		<div className={styles.ExperienceItem}>
			<AccessTimeIcon className={styles.TimeIcon}/>
			<Grid container>
				<Grid item xs={checkPrint ? 4 : 12} sm={6} md={4} lg={4}>
					<h4>{workTime}</h4>
				</Grid>
				<Grid item xs={checkPrint ? 4 : 12} sm={6} md={4} lg={4}>
					<h4>{position}</h4>
				</Grid>
				<Grid item xs={checkPrint ? 4 : 12} sm={6} md={4} lg={4}>
					<h4>{duty}</h4>
				</Grid>
			</Grid>

			<List subheader={<h4>{t("experience.dutyTitle")}</h4>}>
				{dutyList.map((item, index) => {
					return (
						<ListItem key={item + index}><span dangerouslySetInnerHTML={{__html: item}}/></ListItem>
					)
				})}
			</List>
			<List subheader={<h4>{t("experience.summary")}</h4>}>
				{summaryList.map((item, index) => {
					return (
						<ListItem key={item + index}><span dangerouslySetInnerHTML={{__html: item}}/></ListItem>
					)
				})}
			</List>
		</div>
	)
}


// 
export default function Experience() {

	const {t} = useTranslation();

	function formatExp(expTypeName = "") {

		let exp = {}
		if (expTypeName) {
			exp = {
				workTime: t(`experience.${expTypeName}.workTime`),
				position: t(`experience.${expTypeName}.position`),
				duty: t(`experience.${expTypeName}.duty`),
				dutyDescription: t(`experience.${expTypeName}.dutyDescription`),
				summaryDescription: t(`experience.${expTypeName}.summaryDescription`)
			}
		}
		return exp
	}

	return (
		<React.Fragment>
			<h2>{t("experience.title")}</h2>
			<ExperienceItem exp={formatExp("exp0")}/>
			<ExperienceItem exp={formatExp("exp1")}/>
			<ExperienceItem exp={formatExp("exp2")}/>
			{/*<ExperienceItem exp={formatExp("exp3")}/>*/}
			{/*<ExperienceItem exp={formatExp("exp4")}/>*/}
		</React.Fragment>
	)
}
